"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import { Badge } from "@/components/ui/badge";
import { ScrollArea } from "@/components/ui/scroll-area";
import { type Mail } from "../data";

interface MailListProps {
  items: Mail[];
  selectedMail: Mail | null;
  onSelectMail: (mail: Mail) => void;
}

export function MailList({ items, selectedMail, onSelectMail }: MailListProps) {
  return (
    <ScrollArea className="h-[calc(100vh-3.5rem)]">
      <div className="flex flex-col gap-2 p-4 pt-0">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold">Inbox</h2>
          <Badge variant="secondary" className="ml-auto">
            {items.length}
          </Badge>
        </div>
        <div className="flex flex-col gap-2">
          {items.map((item) => (
            <button
              key={item.id}
              className={cn(
                "flex flex-col items-start gap-2 rounded-lg border p-3 text-left text-sm transition-all hover:bg-accent",
                selectedMail?.id === item.id && "bg-muted",
              )}
              onClick={() => onSelectMail(item)}
            >
              <div className="flex w-full flex-col gap-1">
                <div className="flex items-center">
                  <div className="flex items-center gap-2">
                    <div className="font-semibold">{item.from}</div>
                    {!item.read && (
                      <span className="flex h-2 w-2 rounded-full bg-blue-600" />
                    )}
                  </div>
                  <div
                    className={cn("ml-auto text-xs", "text-muted-foreground")}
                  >
                    {new Date(item.date).toLocaleDateString()}
                  </div>
                </div>
                <div className="text-xs font-medium">{item.subject}</div>
                <div className="text-xs text-muted-foreground">
                  {item.message.substring(0, 100)}...
                </div>
                <div className="flex items-center gap-1">
                  {item.labels.map((label) => (
                    <Badge
                      key={label}
                      variant="secondary"
                      className="rounded-sm px-1 font-normal"
                    >
                      {label}
                    </Badge>
                  ))}
                </div>
              </div>
            </button>
          ))}
        </div>
      </div>
    </ScrollArea>
  );
}
