"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import {
  ResizableHandle,
  ResizablePanel,
  ResizablePanelGroup,
} from "@/components/ui/resizable";
import { Separator } from "@/components/ui/separator";
import { TooltipProvider } from "@/components/ui/tooltip";
import { AccountSwitcher } from "./account-switcher";
import { MailList } from "./mail-list";
import { MailDisplay } from "./mail-display";
import { defaultLayout, defaultMails, mailCategories, mailFolders, type Mail } from "../data";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Search } from "lucide-react";

interface MailProps {
  defaultLayout?: number[];
  defaultCollapsed?: boolean;
  navCollapsedSize: number;
}

export function Mail({
  defaultLayout = defaultLayout,
  defaultCollapsed = false,
  navCollapsedSize,
}: MailProps) {
  const [isCollapsed, setIsCollapsed] = React.useState(defaultCollapsed);
  const [mails] = React.useState<Mail[]>(defaultMails || []);
  const [selectedMail, setSelectedMail] = React.useState<Mail | null>(
    mails.length > 0 ? mails[0] : null,
  );
  const [mailFilter, setMailFilter] = React.useState<string>("all");

  return (
    <TooltipProvider delayDuration={0}>
      <ResizablePanelGroup
        direction="horizontal"
        onLayout={(sizes: number[]) => {
          document.cookie = `react-resizable-panels:layout:mail=${JSON.stringify(
            sizes,
          )}`;
        }}
        className="h-full max-h-[800px] items-stretch"
      >
        <ResizablePanel
          defaultSize={defaultLayout[0]}
          collapsedSize={navCollapsedSize}
          collapsible={true}
          minSize={15}
          maxSize={40}
          onCollapse={() => {
            setIsCollapsed(true);
            document.cookie = `react-resizable-panels:collapsed=${JSON.stringify(
              true,
            )}`;
          }}
          onResize={() => {
            setIsCollapsed(false);
            document.cookie = `react-resizable-panels:collapsed=${JSON.stringify(
              false,
            )}`;
          }}
          className={cn(
            isCollapsed &&
              "min-w-[50px] transition-all duration-300 ease-in-out",
          )}
        >
          <div className="flex h-full flex-col">
            <div
              className={cn(
                "flex h-[52px] items-center justify-center",
                isCollapsed ? "h-[52px]" : "px-2",
              )}
            >
              <AccountSwitcher isCollapsed={isCollapsed} />
            </div>
            <Separator />
            <div className="flex-1">
              <div className="px-3 py-2">
                <div className="space-y-1">
                  {mailCategories.map((category) => (
                    <Button
                      key={category.name}
                      variant={category.name === "Inbox" ? "secondary" : "ghost"}
                      size="sm"
                      className="w-full justify-start"
                    >
                      {category.name}
                      {category.count > 0 && (
                        <span className="ml-auto text-xs font-medium">
                          {category.count}
                        </span>
                      )}
                    </Button>
                  ))}
                </div>
              </div>
              <Separator />
              <div className="px-3 py-2">
                <div className="space-y-1">
                  {mailFolders.map((folder) => (
                    <Button
                      key={folder.name}
                      variant="ghost"
                      size="sm"
                      className="w-full justify-start"
                    >
                      {folder.name}
                      {folder.count > 0 && (
                        <span className="ml-auto text-xs font-medium">
                          {folder.count}
                        </span>
                      )}
                    </Button>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </ResizablePanel>
        <ResizableHandle withHandle />
        <ResizablePanel defaultSize={defaultLayout[1]} minSize={30}>
          <div className="flex h-full flex-col">
            <div className="flex items-center p-2">
              <div className="relative flex-1">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input 
                  placeholder="Search" 
                  className="pl-8"
                />
              </div>
            </div>
            <Separator />
            <div className="flex-1 overflow-auto">
              <div className="border-b px-4 py-2 font-semibold">
                Inbox
              </div>
              <div className="flex items-center gap-2 p-2">
                <Button variant="ghost" size="sm" className="text-xs">All mail</Button>
                <Button variant="ghost" size="sm" className="text-xs">Unread</Button>
              </div>
              <MailList
                items={mails}
                selectedMail={selectedMail}
                onSelectMail={setSelectedMail}
              />
            </div>
          </div>
        </ResizablePanel>
        <ResizableHandle withHandle />
        <ResizablePanel defaultSize={defaultLayout[2]} minSize={30}>
          {selectedMail ? (
            <MailDisplay mail={selectedMail} />
          ) : (
            <div className="p-8 text-center text-muted-foreground">
              No message selected
            </div>
          )}
        </ResizablePanel>
      </ResizablePanelGroup>
    </TooltipProvider>
  );
}
